var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('h1',[_vm._v(" Изисквания за устройството и експлоатацията на гъбарници НАРЕДБА № 7 ")]),_c('p',[_c('b',[_vm._v("НАРЕДБА № 7 за хигиенните изисквания за устройството и експлоатацията на гъбарници")])]),_c('p',[_vm._v(" Издадена от министъра на народното здраве, обн., ДВ, бр. 28 от нана6.04.1984 г. ")]),_c('p',[_vm._v(" Чл. 1. Тази наредба урежда хигиенните изисквания за устройството и експлоатацията на гъбарници от стопански организации и в личния двор на граждани."),_c('br'),_vm._v(" Чл. 2. (1) За гъбарници могат да се ползуват:"),_c('br'),_vm._v(" 1. специално построени сгради;"),_c('br'),_vm._v(" 2. полиетиленови оранжерии;"),_c('br'),_vm._v(" 3. сутеренни помещения на жилищни сгради с железобетонни подове;"),_c('br'),_vm._v(" 4. сутеренни помещения на сгради за административно-обществени нужди."),_c('br'),_vm._v(" (2) В жилищни сгради гъбарници се разкриват след получаване съгласието на всички съсобственици. В гъбарниците по ал. 1, т. 3 и 4 може да се използува само пастьоризиран компост."),_c('br'),_vm._v(" (3) Не се разрешава устройството на гъбарници в сгради на лечебно-профилактични заведения, детски ясли и градини, хранителни обекти и складове за хранителни продукти."),_c('br'),_vm._v(" Чл. 3. (1) Гъбарниците се изграждат по проекти на Централния кооперативен съюз (ЦКС)."),_c('br'),_vm._v(" (2) Гъбарниците се одобряват и контролират от хигиенно-епидемиологичните инспекции (ХЕИ)."),_c('br'),_vm._v(" Чл. 4. (1) В гъбарниците се осигурява за ползуване вода, отговаряща на БДС 2823-83 “Вода за пиене”."),_c('br'),_vm._v(" (2) Отпадъчните води от гъбарниците се отвеждат в селищната канализация, а когато са само от мивки, могат да се заустят в попивен кладенец."),_c('br'),_vm._v(" Чл. 5. (1) Подовете на гъбарниците трябва да бъдат гладки и да не пропускат вода. В гъбарниците, изградени от стопански организации, се осигурява наклон към необходимите сифони."),_c('br'),_vm._v(" (2) Между стелажите в гъбарниците трябва да има пътеки с широчина най-малко 60 см, позволяващи свободно ръчно обработване."),_c('br'),_vm._v(" (3) В гъбарниците се осигурява температура от 16 до 24?С в зависимост от технологичните изисквания, относителна влажност 90 – 95 на сто и въздухообмен 12 – 15 м3 на мќ площ за 1 час."),_c('br'),_vm._v(" (4) В гъбарниците в зависимост от капацитета им се осигурява вентилация за необходимия въздухообмен в помещенията, устроена така, че да не замърсява околната среда и да не се превишават във въздуха на работната среда пределно допустимите концентрации за вредните вещества, използувани при производството."),_c('br'),_vm._v(" Чл. 6. Стопанските организации осигуряват за работещите в гъбарниците:"),_c('br'),_vm._v(" 1. санитарен възел с гардеробчета за собственото и работното облекло, душове и тоалетни за мъже и жени в зависимост от броя им;"),_c('br'),_vm._v(" 2. помещение за почивка и хранене;"),_c('br'),_vm._v(" 3. помещение за работен инвентар и складове за препарати;"),_c('br'),_vm._v(" 4. закрити съдове за отпадъци."),_c('br'),_vm._v(" Чл. 7. (1) За производството на гъби се използуват конски и други органични торове и слама (като компост), както и изкуствени торове."),_c('br'),_vm._v(" (2) Строителството на площадки за компост на гъбопроизводство, включително камери за термична обработка, се разрешава на разстояние най-малко 500 м от жилищните райони на населените места."),_c('br'),_vm._v(" Чл. 8. (1) В жилищния двор могат да се изграждат за собствени нужди камери за термична обработка (пастьоризиране) на компост, разположени на най-малко 5 м от уличните платна и от жилищните сгради."),_c('br'),_vm._v(" (2) Пред камерите по ал. 1 се устройват циментирани площадки за стоварване на суровия компост и за пълнене в торби при изваждането на пастьоризирания компост."),_c('br'),_vm._v(" (3) Компостът от производствените площадки може да се превозва в насипно състояние, като се вземат мерки за предотвратяване разпиляването му."),_c('br'),_vm._v(" Чл. 9. (1) Използуваният компост след прибиране на реколтата от съответното плододаване се изнася в торби или други съдове, без да се допуска замърсяване на околната среда. Същият може да се употреби за наторяване със заораване или се извозва на сметищата след обеззаразяване с разрешени растителнозащитни препарати, посочени в списък на Министерството на народното здраве и Националния аграрно-промишлен съюз (изменян и допълван ежегодно)."),_c('br'),_vm._v(" (2) При разкриване на гъбарници в естествени хранилища в паркове или в помещения на обществени сгради в населените места се вземат мерки за недопускане замърсяването на околната среда при зареждането им с компост, изнасянето му след плододаването и извършването на необходимата дезинфекция, като се огражда пространството пред входа с подходящи плоскости."),_c('br'),_vm._v(" Чл. 10. (1) В гъбарниците и камерите за пастьоризация на компоста се използуват само препарати съгласно Указание за прилагане на препарати за дезинфекция, дезинсекция и дератизация на Министерството на народното здраве (необнародвано)."),_c('br'),_vm._v(" (2) Основната дезинфекция на гъбарниците се извършва само в периода между реколтите в празни, почистени и измити помещения."),_c('br'),_vm._v(" (3) Употребата на химични препарати в гъбарниците през беритбения период става само под контрола на специализираната растителнозащитна лаборатория към Обединено кооперативно предприятие “Билкокооп”, за да не се използуват препарати, вредни за консуматорите."),_c('br'),_vm._v(" Чл. 11. Отглеждането на гъби се извършва само с мицели, получени от съответната стопанска организация, която договаря и изкупува продукцията."),_c('br'),_vm._v(" Чл. 12. (1) Събирането и ръчната обработка на гъбите за пазара се извършват съгласно изискванията на ЦКС."),_c('br'),_vm._v(" (2) Бракуваните гъби и остатъците от почистването се събират ежедневно в съдове за смет и след дезинфекция се изсипват в изкопана за целта яма, дълбока на